@font-face {
  font-family: "Avenir-Book";
  src: url("./fonts/Avenir-Book.ttf");
}

.input_big {
  margin: 20px 0;
  padding: 24px 62px;
  width: 100%;
  height: 70px;
  display: block;
  background: url(images/Search.svg) no-repeat 25px 24px;
  color: #3d4e61;
  font-size: 16px;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  font-family: "Avenir-Book", sans-serif;
}

.input_small {
  margin: 20px 0;
  padding: 20px 33px;
  display: block;
  background: url(images/Search.svg) no-repeat 12px 14px;
  background-size: 11px;
  color: #3d4e61;
  font-size: 14px;
  width: 100%;
  height: 42px;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  font-family: "Avenir-Book", sans-serif;
}

input:hover {
  border: 1px solid #e1e7ed;
  box-sizing: border-box;
  box-shadow: 1px 3px 8px #3d4e61;
  border-radius: 4px;
  font-family: "Avenir-Book", sans-serif;
}

input:focus {
  font-family: "Avenir-Book", sans-serif;
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
}
